/* Menu.css */
.navbar {
  width: 100%;
  background-color: #333; /* Exempel på bakgrundsfärg */
}

.menu-container {
  display: flex;
  align-items: center; /* Centrerar elementen vertikalt */
  justify-content: start; /* Startar element från vänster kant */
  padding: 10px;
}

.nav-links {
  list-style: none;
  display: flex;
  align-items: center; /* Centrerar länkarna vertikalt */
  margin-left: 20px; /* Ger lite utrymme mellan logotypen och länkarna */
}

.nav-link {
  text-decoration: none;
  color: white; /* För en ljus textfärg */
  margin: 0 15px; /* Ger utrymme runt länkarna */
}

.rotating-logo {
  animation: rotateAnimation 20s linear infinite;
  height: 50px; /* Anpassar storleken för att passa menyn */
  margin-right: 10px; /* Lägg till margin om det behövs för justering */
}

@keyframes rotateAnimation {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
